import React from "react";
import PropTypes from "prop-types";
import FullProductRow from "../fix/FullProductRow"

const CommonProductEntry = (props) => {
    const twoButtons = props.product.changeable && props.onChange && props.onDetails;
    return (
            <div className="u-position-relative">
                <div className="u-padding-left-l u-padding-right-l-xl">
                    <FullProductRow {...props}
                                    borderTop={false}
                                    forFreeLabel={props.forFreeLabel}
                                    serviceFreeLabel={props.serviceFreeLabel}
                                    lowerInstallmentsClicked={props.lowerInstallmentsClicked}
                                    descriptions={props.descriptions}
                                    showActionsMobile={false}/>
                    <div className="l-row u-border-top u-medium-hide u-large-hide">
                        {
                            props.product.changeable && props.onChange &&
                            <div className={`l-col l-col--auto l-col-small-${twoButtons ? '6 u-small-border-right' : '12'} u-spacing-right-l u-padding-l u-padding-top-l u-padding-left-s u-small-no-spacing-r u-small-text-center`}>
                                <a href="#" className="u-spacing-right-l u-small-no-spacing" onClick={props.onChange}>
                                    {props.changeLabel}
                                </a>
                            </div>
                        }
                        {
                            props.onDetails &&
                            <div className={`l-col l-col--auto l-col-small-${twoButtons ? '6' : '12'} u-spacing-right-l u-padding-l u-padding-top-l u-padding-left-s u-small-no-spacing-r u-small-text-center`}>
                                <a href="#" className="u-spacing-right-l u-small-no-spacing" onClick={props.onDetails}>
                                    {props.detailsLabel}
                                </a>
                            </div>
                        }
                    </div>
                </div>
                {
                    props.product.removable && props.onRemove &&
                    <a href="#" className="opl-checkout__item__remove u-hide u-small-block" onClick={props.onRemove}>
                        <span className="u-acc-hide">{props.removeLabel}</span>
                    </a>
                }
            </div>
    )
};

CommonProductEntry.propTypes = {
    product: PropTypes.shape({
        price: PropTypes.shape({
            oneTimeCost: PropTypes.shape({
                gross: PropTypes.string,
                currency: PropTypes.string
            }),
            monthlyCosts: PropTypes.arrayOf(
                PropTypes.shape({
                    gross: PropTypes.string,
                    currency: PropTypes.string,
                    monthFrom: PropTypes.number,
                    monthTo: PropTypes.number
                })
            ),
            installments: PropTypes.bool,
        }),
        code: PropTypes.string,
        name: PropTypes.string,
        thumbnailIcon: PropTypes.string,
        imageUrl: PropTypes.string,
        description: PropTypes.string,
        changeable: PropTypes.bool,
        removable: PropTypes.bool,
    }),
    detailsLabel: PropTypes.string,
    forFreeLabel: PropTypes.string,
    changeLabel: PropTypes.string,
    removeLabel: PropTypes.string,
    onDetails: PropTypes.func,
    onChange: PropTypes.func,
    onRemove: PropTypes.func,
    lowerInstallmentsClicked: PropTypes.func,
    serviceFreeLabel: PropTypes.string,
}

CommonProductEntry.defaultProps = {
    changeLabel: "Zmień",
    detailsLabel: "Szczegóły",
    removeLabel: "Usuń",
}

export default CommonProductEntry;